.header {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 350px;
  width: 100%;
  .header-container {
    img {
      width: 100%;
      height: 100%;
    }
  }
  @media (max-width: 600px) {
    height: 200px;
  }
  @include mobile {
    height: 150px;
  }
}
